<template>
    <div class="seeHistory">
        <div class="seeHistoryHeader">
            <div class="seeHistoryHeader_content">
                <span class="return" @click="back"><img src="../../../assets/return.png" width="100%" alt=""></span>
                <span class="title">历史查看</span>
                <span class="menu" @click="showMenu('搜索')"><img src="../../../assets/menu.png" width="100%"
                                                                alt=""></span>
            </div>
        </div>
        <div class="seeHistoryContent">
            <div class="noCarImg" v-if="noCarImg"><img src="./seeHistory@2x.png" alt=""></div>
            <ul class="carLists" v-else>
                <li class="carLists_item" v-for="historyList in historyLists">
                    <dl @click="toNative(historyList.vehicle_id)">
                        <dt>
                            <img v-if="historyList.thumbnail != null" width="100%" height="100%"
                                 :src="historyList.thumbnail" alt="">
                            <img v-else width="100%" height="100%" src="../../../assets/noImg.jpg" alt="">
                            <div class="remark"
                                 v-show="historyList.commodity_status == 3||historyList.commodity_status == 4||historyList.commodity_status == 5||historyList.commodity_status == 6">
                                <p>已出售</p>
                            </div>
                        </dt>
                        <dd>
                            <p><span>{{historyList.title}}</span></p>
                            <p>{{historyList.first_register_date | listTime}}/{{historyList.mileage}}万公里</p>
                            <p class="carsPrice">
                    <span class="amount">{{historyList.original_price}}
                      <span class="wan">万</span>
                    </span>
                                <span v-for="tag in historyList.tags" class="tag"
                                      :class="{hand : tag == '练手', suv : tag == 'SUV', beNew : tag == '准新'}">{{tag}}</span>
                            </p>
                        </dd>
                    </dl>
                    <!--<span :class="historyList.is_in_look == 0? 'seeBtn' : 'seeBtn_two' " @click="addCarList(historyList)">{{historyList.is_in_look == 0 ? '加入看车单' : '已加入车单'}}</span>-->
                </li>
            </ul>
        </div>
        <div class="scrollTop" @click="gotop" v-show="isScroll"></div>
        <menuPage ref="menuMark" @close="closeThis"></menuPage>
    </div>
</template>
<script>
    import menuPage from "../../public/nav.vue";
    import Vue from "vue";

    export default {
        data() {
            return {
                ifSeen: true,
                nativeHref: '',
                signSee: '',
                noCarImg: false,
                customerId: '',
                tokens: '',
                showMenuPage: false,
                area: false,
                carStatus: false,
                isScroll: false,
                historyLists: '',
                areas: [
                    {content: '全部展'},
                    {content: 'A'},
                    {content: 'B'},
                    {content: 'C'},
                    {content: 'D'},
                    {content: 'E'},
                    {content: 'F'},
                ],
                status: [
                    {content: '全部车源状态'},
                    {content: '只看在售车辆'},
                    {content: '只看已售出车辆'},
                ],
            }
        },
        mounted() {
            this.tokens = JSON.parse(window.localStorage.getItem("user")).token;
            this.customerId = JSON.parse(window.localStorage.getItem("user")).customer;
            var seeHistory = window.localStorage.getItem("History");
            if (seeHistory) {
                this.historyLists = JSON.parse(seeHistory);
            } else {
                this.noCarImg = true;
            }
            window.addEventListener('scroll', this.showTop);
        },
        methods: {
            toNative(msg) {
                var _this = this;
                if (_this.native.isAndroid) {
                    if (_this.nativeHref.indexOf("native") > 0) {
                        window.location.href = '#/detail/' + msg + '?native';
                    } else {
                        _this.$router.push('./detail/' + msg);
                    }
                } else if (_this.native.isiOS) {
                    if (_this.nativeHref.indexOf("native") > 0) {
                        window.location.href = '#/detail/' + msg + '?native';
                    } else {
                        _this.$router.push('./detail/' + msg);
                    }
                } else {
                    _this.$router.push('./detail/' + msg);
                }
            },
            addCarList: function (historyList) {
                var _this = this;
                if (historyList.is_in_look == 1) {
                    return;
                } else {
                    $.ajax({
                        type: "post", url: this.global.host + "/watchList/add",
                        data: {
                            customerId: _this.customerId,
                            token: _this.tokens,
                            vehicleId: historyList.vehicle_id
                        },
                        success: function (response) {
                            if (response.resultCode == '0000') {
                                historyList.is_in_look = 1;
                            } else if (response.resultCode == '1111') {

                            }
                        }
                    });
                }
            },
            showTop() {
                var curHeight = document.documentElement.scrollTop || document.body.scrollTop;
                if (curHeight > 100) {
                    this.isScroll = true
                } else {
                    this.isScroll = false
                }
            },
            gotop() {
                $(window).scrollTop(0);
            },
            back() {
                this.$router.back(-1);
            },
            showMenu(msg) {
                this.$refs.menuMark.show(msg, $(".seeHistory"));
            },
            closeThis() {
                this.showThis = false;
            },
            showArea() {
                this.area = !this.area
                this.carStatus = false
                this.signStatus = false
            },
            showCarStatus() {
                this.carStatus = !this.carStatus
                this.area = false
                this.signStatus = false
            },
        },
        components: {
            menuPage,
        }
    }
    Vue.filter('listTime', function (value) {//value为13位的时间戳
        function addt(m) {
            return m < 10 ? '0' + m : m
        }

        var time = new Date(parseInt(value));
        var y = time.getFullYear();
        var m = time.getMonth() + 1;
        var d = time.getDate();

        return y + '年' + addt(m) + '月';
    });
</script>
<style rel="stylesheet/scss" lang="scss">
    .seeHistory {
        position: absolute;
        background: #f5f5f5;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        overflow: auto;
        .seeHistoryHeader {
            width: 100%;
            box-sizing: border-box;
            padding: .2rem .4rem;
            background: #ffffff;
            border-bottom: 1px solid #ececec;
            .seeHistoryHeader_content {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .return {
                    width: 0.19rem;
                    height: 0.34rem;
                }
                .title {
                    font-size: 0.36rem;
                    color: #000000;
                }
                .menu {
                    width: 0.32rem;
                    height: 0.32rem;
                }
            }
        }
        .seeHistoryContent {
            height: auto;
            box-sizing: border-box;
            padding: 0 .4rem;
            background: #ffffff;
            .noCarImg {
                width: 100%;
                height: 100%;
                margin-top: 0.1rem;
                text-align: center;
                background: #ffffff;
                img {
                    width: 4rem;
                    height: 4.22rem;
                    margin-top: 0.6rem;
                }
            }
            .carLists {
                height: 100%;
                background: #ffffff;
                .carLists_item {
                    display: flex;
                    flex-direction: column;
                    position: relative;
                    border-bottom: 1px solid #eeeeee;
                    width: 100%;
                    dl {
                        display: flex;
                        box-sizing: border-box;
                        padding: .3rem 0;
                        position: relative;
                        dt {
                            width: 2.2rem;
                            height: 1.64rem;
                            .remark {
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 100%;
                                height: 100%;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                background: rgba(0, 0, 0, 0.2);
                                p {
                                    border: 1px solid white;
                                    -webkit-border-radius: 0.1rem;
                                    -moz-border-radius: 0.1rem;
                                    border-radius: 0.1rem;
                                    color: #ffffff;
                                    font-size: 0.3rem;
                                    padding: 0.20rem 0.25rem;
                                }
                            }
                        }
                        dd {
                            display: flex;
                            width: 4.68rem;
                            flex-direction: column;
                            margin-left: 0.2rem;
                            position: relative;
                            p:nth-child(1) {
                                display: flex;
                                font-weight: bold;
                                span:nth-child(1) {
                                    font-size: 0.28rem;
                                    line-height: 0.38rem;
                                    margin-top: -0.05rem;
                                }
                            }
                            p:nth-child(2) {
                                margin-top: 0.1rem;
                                font-size: 0.24rem;
                            }
                            .carsPrice {
                                position: absolute;
                                left: 0;
                                bottom: 0;
                                color: #ff4340;
                                display: flex;
                                .amount {
                                    font-size: 0.3rem;
                                    line-height: 1;
                                    .wan {
                                        font-size: .24rem;
                                    }
                                }
                                span:not(:first-child) {
                                    margin-left: 0.1rem;
                                }
                                .tag {
                                    font-size: .22rem;
                                    height: .22rem;
                                    line-height: 1;
                                    display: inline-block;
                                    -webkit-border-radius: .04rem;
                                    -moz-border-radius: .04rem;
                                    border-radius: .04rem;
                                }
                                .hand {
                                    border: 1px solid #65dd6d;
                                    color: #65dd6d;
                                }
                                .beNew {
                                    border: 1px solid #ff7c7c;
                                    color: #ff7c7c;
                                }
                                .suv {
                                    border: 1px solid #73dffe;
                                    color: #73dffe;
                                }
                            }
                        }
                    }
                    .seeBtn {
                        padding: 0.05rem 0.2rem;
                        display: block;
                        border: 1px solid #2d8bff;
                        -webkit-border-radius: 10px;
                        -moz-border-radius: 10px;
                        border-radius: 10px;
                        color: #2d8bff;
                        font-size: 0.3rem;
                        position: absolute;
                        right: 0.24rem;
                        bottom: 0.15rem;
                    }
                    .seeBtn_two {
                        padding: 0.05rem 0.2rem;
                        display: block;
                        background: #f3f2f2;
                        -webkit-border-radius: 10px;
                        -moz-border-radius: 10px;
                        border-radius: 10px;
                        color: #dddddd;
                        font-size: 0.3rem;
                        position: absolute;
                        right: 0.24rem;
                        bottom: 0.15rem;
                    }
                }
            }
        }
        .scrollTop {
            position: fixed;
            bottom: 0.5rem;
            right: 0.2rem;
            border-radius: 2.5rem;
            width: 0.8rem;
            height: 0.8rem;
            background: url('./topIcon.png') no-repeat;
            background-size: 100%;
            z-index: 999;
            display: block;
        }
    }

    .move-enter-active {
        transition: all .2s linear;
        transform: translate3d(0, 0, 0);
    }

    .move-leave-active {
        transition: all .2s linear;
        transform: translate3d(100%, 0, 0);
    }

    .move-enter, .move-leave {
        transform: translate3d(100%, 0, 0)
    }
</style>



